import React, { useState, useEffect } from "react";
import ReactMarkdown from "react-markdown";
import { FaUserAstronaut, FaChevronLeft } from "react-icons/fa";
import { GoCalendar } from "react-icons/go";

import "./styles.css";

import { Card, Container } from "react-bootstrap";
import { Link } from "react-router-dom";

export default function Post(props) {
  const [post, setPost] = useState({
    title: "No title",
    body: "teste",
    createdAt: { day: "2020", hour: "20" },
    author: "Canoi"
  });

  const { postId } = props.match.params;
  console.log(postId);
  var tag = document.createElement("script");
  useEffect(() => {
    const jsonData = require(`../../pages/Posts/${postId}/data.json`);
    const markdownPost = require(`../../pages/Posts/${postId}/post.md`);
    fetch(markdownPost)
      .then(res => res.text())
      .then(text => {
        jsonData.body = text;
        setPost(jsonData);
      });

    return () => {
      tag.remove();
    };
  }, []);

  useEffect(() => {
    const pre = document.getElementsByTagName("pre");
    //console.log(pre);
    //pre.className = "prettyprint"
    for (let i = 0; i < pre.length; i++) {
      pre[i].setAttribute("class", "prettyprint");
    }
    window.PR.prettyPrint();
  }, [post]);

  return (
    <Container
      className="content-wrapper"
      style={{ margin: "50px auto" }}
      fluid
    >
      <Card className="post-wrapper">
        <Card.Body className="post">
          <Link to="/"><FaChevronLeft style={{marginBottom: '2px'}} /> <span>Back</span></Link>
          <Card.Title className="post-title" style={{ textAlign: "center" }}>
            {post.title}
          </Card.Title>
          <Card.Subtitle
            style={{ textAlign: "center" }}
            className="text-muted font-weight-normal post-subtitle"
          >
            {post.description}
          </Card.Subtitle>
          <hr/>
          <Card.Text as="div" className="post-body">
            <ReactMarkdown source={post.body} />
          </Card.Text>
        </Card.Body>
        <Card.Footer>
          <Card.Link className="post-footer-text">
            <GoCalendar />{" "}
            <span>{post.createdAt.day + " " + post.createdAt.hour}</span>
          </Card.Link>
          <Card.Link>
            <FaUserAstronaut /> <span>{post.author}</span>
          </Card.Link>
        </Card.Footer>
      </Card>
    </Container>
  );
}
