import React, { useState, useEffect } from "react";
import { Figure, Row, Col } from "react-bootstrap";

import "./styles.css";

// import { Container } from './styles';

const Header = () => {
  const [data, setData] = useState({});

  useEffect(() => {
    fetch("https://api.github.com/users/canoi12")
      .then(res => res.json())
      .then(data => {
        console.log(data);
        setData(data);
      });
  }, []);

  return (
    <header className="header">
      <Row className="m-0" style={{padding: '16px'}}>
        <Col xs={12}>
          <Figure>
            <Figure.Image
              width={128}
              height={128}
              src={data.avatar_url}
              roundedCircle
              fluid
            />
          </Figure>
        </Col>
        <Col xs={12}>
          <h1 className="text-white">{data.name}</h1>
        </Col>
        <Col>
          <h2 className="text-muted" style={{fontSize: '18px'}}>{data.bio}</h2>
        </Col>
      </Row>
    </header>
  );
};

export default Header;
