import React, { useState, useEffect } from "react";
import { Container, Row, Col, Card } from "react-bootstrap";
import { FaUserAstronaut } from "react-icons/fa";
import { GoCalendar } from "react-icons/go";

import { Link } from 'react-router-dom';

import "./styles.css";
import postsFile from '../../pages/Posts/posts.json';

export default function Posts() {
  const [posts, setPosts] = useState([]);
  
  useEffect(()=>{
    let postsAux = [];
    postsFile.map(post => {
      const jsonData = require(`../../pages/Posts/${post}/data.json`);
      jsonData.id = post;
      postsAux.push(jsonData);
    });
    setPosts(postsAux);
  }, [])

  return (
    <Container
      className="content-wrapper"
      style={{ marginBottom: "25px" }}
      fluid
    >
      <Row style={{ margin: "25px 0px" }}>
        <Col>
          <span className="title text-white">posts</span>
          <br />
          <span className="subtitle text-white">some articles i've write</span>
        </Col>
      </Row>
      <Row>
        {posts.map(post => (
          <Col key={post.title} xs={12} sm={6} md={4} style={{ marginBottom: "8px" }}>
            <Card style={{ height: "100%" }}>
              <Card.Body>
                <Card.Title className="post-card-title">
                  <Link to={`/post/${post.id}`}>
                    {post.title}
                  </Link>
                </Card.Title>
                <Card.Subtitle className="post-card-description text-muted text-left mb-2">
                  {post.description}
                </Card.Subtitle>
              </Card.Body>
              <Card.Footer>
                <Card.Link className="post-card-links">
                  <FaUserAstronaut style={{ color: "#3b3b3b" }} />{" "}
                  <span>{post.author}</span>
                </Card.Link>
                <Card.Link className="post-card-links">
                  <GoCalendar style={{ color: "#3b3b3b" }} />{" "}
                  <span>{post.createdAt.day}</span>
                </Card.Link>
              </Card.Footer>
            </Card>
          </Col>
        ))}
        <Col xs={12} sm={6} md={4} style={{ marginBottom: "8px" }}>
          <Card style={{ height: "100%" }}>
            <Card.Body
              className="justify-content-center align-items-center"
              style={{ display: "flex" }}
            >
              <Card.Text style={{ fontSize: "20px" }}>
                <a href="https://github.com/canoi12">More...</a>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
}
